"use client"

import { useState } from "react"
import { useMutation } from "@tanstack/react-query"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Badge } from "@/components/ui/badge"
import { Separator } from "@/components/ui/separator"
import { Accordion, AccordionContent, AccordionItem, AccordionTrigger } from "@/components/ui/accordion"
import { Nav } from "@/components/nav"
import { JsonViewer } from "@/components/json-viewer"
import { LatencyBadge } from "@/components/latency-badge"
import { batchPath, type BatchResult } from "@/lib/api"
import { EXAMPLE_BATCH_JSON_PATH, EXAMPLE_BATCH_PDFS_ROOT } from "@/lib/examples"
import { useToast } from "@/hooks/use-toast"
import { Play, Download, Copy, Sparkles, CheckCircle2, XCircle, Loader2 } from "lucide-react"

export default function BatchPage() {
  const [jsonPath, setJsonPath] = useState("")
  const [pdfsRootPath, setPdfsRootPath] = useState("")
  const [results, setResults] = useState<BatchResult[]>([])
  const { toast } = useToast()

  const batchMutation = useMutation({
    mutationFn: async () => {
      setResults([])
      return batchPath({ json_path: jsonPath, pdfs_root_path: pdfsRootPath })
    },
    onSuccess: (result) => {
      setResults(result.data)
      toast({ title: "Batch processing completed", description: `Processed ${result.data.length} files` })
    },
    onError: (error: any) => {
      toast({ title: "Batch processing failed", description: error.message, variant: "destructive" })
    },
  })

  const handleRunBatch = () => {
    if (!jsonPath || !pdfsRootPath) {
      toast({ title: "All fields are required", variant: "destructive" })
      return
    }

    batchMutation.mutate()
  }

  const handleLoadExample = () => {
    setJsonPath(EXAMPLE_BATCH_JSON_PATH)
    setPdfsRootPath(EXAMPLE_BATCH_PDFS_ROOT)
    toast({ title: "Example loaded" })
  }

  const handleExportAll = () => {
    const blob = new Blob([JSON.stringify(results, null, 2)], { type: "application/json" })
    const url = URL.createObjectURL(blob)
    const a = document.createElement("a")
    a.href = url
    a.download = "batch-results.json"
    a.click()
    URL.revokeObjectURL(url)
    toast({ title: "Results exported" })
  }

  const handleCopyAll = async () => {
    await navigator.clipboard.writeText(JSON.stringify(results, null, 2))
    toast({ title: "Results copied to clipboard" })
  }

  return (
    <div className="min-h-screen bg-background">
      <Nav />

      <main className="container mx-auto px-4 py-8">
        <div className="max-w-4xl mx-auto space-y-6">
          <div>
            <h1 className="text-3xl font-bold mb-2">Batch Processing</h1>
            <p className="text-muted-foreground">Process multiple PDFs in one request</p>
          </div>

          <Card className="rounded-2xl">
            <CardHeader>
              <CardTitle>Batch Configuration</CardTitle>
              <CardDescription>Configure and run batch processing</CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="space-y-2">
                <Label htmlFor="json-path">JSON Path</Label>
                <Input
                  id="json-path"
                  value={jsonPath}
                  onChange={(e) => setJsonPath(e.target.value)}
                  placeholder="./dataset.json"
                />
              </div>

              <div className="space-y-2">
                <Label htmlFor="pdfs-root">PDFs Root Path</Label>
                <Input
                  id="pdfs-root"
                  value={pdfsRootPath}
                  onChange={(e) => setPdfsRootPath(e.target.value)}
                  placeholder="./files"
                />
              </div>

              <Separator className="my-4" />

              <div className="flex items-center justify-between">
                <Button variant="outline" onClick={handleLoadExample}>
                  <Sparkles className="h-4 w-4 mr-1" />
                  Load Example
                </Button>
                <Button onClick={handleRunBatch} disabled={batchMutation.isPending}>
                  {batchMutation.isPending ? (
                    <>
                      <Loader2 className="h-4 w-4 mr-1 animate-spin" />
                      Processing...
                    </>
                  ) : (
                    <>
                      <Play className="h-4 w-4 mr-1" />
                      Run Batch
                    </>
                  )}
                </Button>
              </div>
            </CardContent>
          </Card>

          {results.length > 0 && (
            <Card className="rounded-2xl">
              <CardHeader>
                <div className="flex items-center justify-between">
                  <div>
                    <CardTitle>Results</CardTitle>
                    <CardDescription>{results.length} file(s) processed</CardDescription>
                  </div>
                  <div className="flex gap-2">
                    <Button variant="outline" size="sm" onClick={handleCopyAll}>
                      <Copy className="h-4 w-4 mr-1" />
                      Copy All
                    </Button>
                    <Button variant="outline" size="sm" onClick={handleExportAll}>
                      <Download className="h-4 w-4 mr-1" />
                      Export All
                    </Button>
                  </div>
                </div>
              </CardHeader>
              <CardContent>
                <Accordion type="single" collapsible className="w-full">
                  {results.map((result, index) => (
                    <AccordionItem key={index} value={`item-${index}`}>
                      <AccordionTrigger className="hover:no-underline">
                        <div className="flex items-center gap-3 flex-1">
                          {result.status === "success" ? (
                            <CheckCircle2 className="h-4 w-4 text-accent" />
                          ) : (
                            <XCircle className="h-4 w-4 text-destructive" />
                          )}
                          <span className="font-mono text-sm">{result.filename}</span>
                          <Badge variant={result.status === "success" ? "default" : "destructive"}>
                            {result.status}
                          </Badge>
                          {result.latency_ms && <LatencyBadge latency={result.latency_ms} />}
                        </div>
                      </AccordionTrigger>
                      <AccordionContent>
                        {result.data ? (
                          <JsonViewer data={result.data} filename={`${result.filename}.json`} />
                        ) : (
                          <div className="text-destructive p-4 rounded-xl bg-destructive/10">
                            {result.error || "Unknown error"}
                          </div>
                        )}
                      </AccordionContent>
                    </AccordionItem>
                  ))}
                </Accordion>
              </CardContent>
            </Card>
          )}
        </div>
      </main>
    </div>
  )
}
